body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #141414;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.home-container {
  text-align: center;
  background: rgba(0, 0, 0, 0.75);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.2);
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

p {
  font-size: 1.2rem;
  margin: 0.5rem 0;
}

.button {
  display: inline-block;
  margin: 10px 0;
  padding: 10px 20px;
  background-color: #e50914;
  color: white;
  text-decoration: none;
  font-weight: bold;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #f40612;
}
